import React from 'react'
import Loading from "components/Loading";
import {AjaxByToken} from 'utils/ajax';
import moment from 'moment';
import {Link} from 'react-router';
import {Pagination , Modal} from 'antd';

class PopleInterview extends React.Component{
    constructor(){
        super();
        this.state={
            infoList:[],
            numFound:"",
            isEmpty:false
        }  
    }
    componentDidMount(){
        this.getPersonInfoList('0') 
    }
    //获取人物专访信息列表
    getPersonInfoList = (num) => {
        this.setState({
            isLoading:true
        })
        AjaxByToken('/mobile/api/ad_show/getWxInfo',{
            head: {
                transcode: 'F00012',
                type:"h"
            },
            data: {
                startIndex:num,
                pageNum:"10"
            }
        })
        .then(res=>{
            this.setState({
                infoList:res.data,
                numFound:res.numFound,
                isLoading: false,
                isEmpty:true
            })
        },err=>{
            Modal.error({
                title: `${err.data.returnMsg}`,
              });
        });
    }
    pageChange = (page, pageSize) => {
        this.getPersonInfoList(`${((page-1)*10)}`);
        setTimeout(()=>{
            window.scroll(0,0)
        },100)
        
    }
    backTop = () => {
        window.scroll(0,0)
    }
   
    render(){
        const {infoList , numFound , isLoading ,isEmpty} = this.state;
        return(
            <div className="contentSelect">
                <div className="content">
                    { 
                        isLoading && <Loading/> 
                    }
                    {
                        infoList.length ===0 && isEmpty && 
                        <div className="emptyIcon">
                            <img src="/static/images/emptyIcon.png"/>
                            <e className="emptyContent">暂时没有新的内容</e>
                        </div>
                    }
                    { infoList.length !==0 && 
                        infoList.map((item , index)=>{
                            return(
                                <div className="contentList">
                                    <Link 
                                        className="peopleImg" 
                                        to={{
                                            pathname:"article",
                                            query:{
                                                id:`${item.famousPersonId}`,
                                                title:"人物专访",
                                                people:true
                                            }
                                        }}
                                    >
                                        <img src={item.imageUrl?item.imageUrl:""} alt={item.name}/>
                                    </Link>
                                    <div className="people">
                                        <h2>
                                            <Link 
                                                    to={{
                                                        pathname:"article",
                                                        query:{
                                                            id:`${item.famousPersonId}`,
                                                            title:"人物专访",
                                                            people:true
                                                        }
                                                    }}
                                                >
                                                {item.name?item.name:""}
                                            </Link>
                                        </h2>
                                        <p>
                                            <Link 
                                                to={{
                                                    pathname:"article",
                                                    query:{
                                                        id:`${item.famousPersonId}`,
                                                        title:"人物专访",
                                                        people:true
                                                    }
                                                }}
                                            >
                                                {item.profile?`${item.profile.slice(0,40)} . . . . . .`:""}
                                            </Link>
                                        </p>
                                        {/* <p className="readNumber"><span>{item.redsum?item.redsum:"0"}</span>&nbsp;&nbsp;阅读</p> */}
                                    </div>
                                    
                                </div>
                            )
                        })
                    }
                    {infoList.length !==0 &&
                        <div className="page">
                            <Pagination 
                                defaultCurrent={1} 
                                total={numFound} 
                                onChange= {this.pageChange}
                            />
                            {/* <div className="backTop" onClick={this.backTop}>
                                <img src="/static/images/feiji.png"/>
                            </div> */}
                        </div>
                    }
                </div>
            
        </div>
        )
    }
}
export default PopleInterview


