import {
    NEWS_LIST,
} from '../constants/home';

const initialState = {
    photolist: [], 
};

export default function home(state = initialState,actions){
    switch(actions.type){
        case NEWS_LIST: 
            return {...state,photolist:actions.photolist};      
        default: 
            return state;
    }
} 


