import React from 'react';
import DownSelect from 'components/downSelect';

export default class Share extends React.Component{
    appDownLoad = () => {
        this.props.showAppDownLoad()
    }
    render(){
        const {query , showAppDownLoad,backTop} = this.props;
        return(
            <div>
                <div className="share">
                    <img src="/static/images/wechat.png"/>
                    <a href="https://passport.weibo.cn/signin/login">
                        <img src="/static/images/Weibo.png"/>
                    </a>
                    <a href="https://www.linkedin.com">
                        <img src="/static/images/in.png"/>
                    </a>
                    
                </div>
                <DownSelect showAppDownLoad= {showAppDownLoad}/>
                <div className="foot">
                    ©2014 51jrq.com 沪ICP备14045153号-1
                    <div className="backTop" onClick={backTop}>
                        <img src="/static/images/feiji.png"/>
                    </div>
                </div>
            </div>
        )
    }
}


