import React from 'react';
import Slider from 'components/Slider';
import axios from 'axios';
import moment from 'moment';
import Loading from 'components/Loading';
import {Link} from 'react-router';
import {Pagination } from 'antd';

import {AjaxByToken} from 'utils/ajax';

//redux
// import {bindActionCreators} from 'redux';
// import { connect } from 'react-redux';
// import * as Actions from 'actions';


export default class Home extends React.Component{
    constructor(){
        super();
        this.state={
            infoList:[],//金融圈信息列表
            numFound:"",//查询到的总数
            isLoading:false,
            isEmpty:false
        }
    }
    getContents = () => {
        this.props.getContents()
    }
    componentDidMount(){
        this.getjrqInfoList('0');
    }
    //获取金融圈信息列表
    getjrqInfoList = (num) => {
        this.setState({
            isLoading:true,
        })
        AjaxByToken('/mobile/api/ad_show/getWxInfo',{
            head: {
                transcode: 'F00010',
                type:"h"
            },
            data: {
                startIndex:num,
                pageNum:"10"
            }
        })
        .then(res=>{
            this.setState({
                infoList:res.data,
                numFound:res.numFound,
                isLoading:false,
                isEmpty:true
            })
        },err=>{
            console.log(err)
        });
    }
    pageChange = (page, pageSize) => {
        this.getjrqInfoList(`${((page-1)*10)}`);
        setTimeout(()=>{
            window.scroll(0,0)
        },100)
    }
    backTop = () => {
        window.scroll(0,0)
    }
    render(){
        const { infoList , numFound , isLoading ,isEmpty} = this.state;
        return(
            <div className="contentSelect" id="test">
                <div className="content">
                    {
                        isLoading && <Loading/>
                    }
                    {
                        infoList.length ===0 && isEmpty && 
                        <div className="emptyIcon">
                            <img src="/static/images/emptyIcon.png"/>
                            <e className="emptyContent">暂时没有新的内容</e>
                        </div>
                    }
                    { infoList.length!==0 && 
                        infoList.map((item,index)=>{
                            return (
                                <div className="contentList">
                                    <Link 
                                        className="contentImg" 
                                        to={{
                                            pathname:"article",
                                            query:{
                                                id:`${item.infoid}`,
                                                title:`${item.groupname}`
                                            }
                                        }}
                                    >
                                        <img src={item.photourl?item.photourl:""} alt={item.title}/>
                                    </Link>
                                    <div className="contentDate">
                                        {item.pubdate?moment(item.pubdate).format("YYYY-MM-DD"):"暂无日期"}
                                    </div>
                                        <h2>
                                            <Link  
                                                to={{
                                                    pathname:"article",
                                                    query:{
                                                        id:`${item.infoid}`,
                                                        title:`${item.groupname}`
                                                    }
                                                }}
                                            >
                                                {item.title?item.title:""}
                                            </Link>
                                        </h2>
                                    
                                    <p>
                                        <Link 
                                            to={{
                                                pathname:"article",
                                                query:{
                                                    id:`${item.infoid}`,
                                                    title:`${item.groupname}`
                                                }
                                            }}
                                        >
                                            {item.preview_text?`${item.preview_text.slice(0,70)}  . . . . . .`:""}
                                        </Link>
                                    </p>
                                    {/* <p className="readNumber">
                                        <span>{item.redsum?item.redsum:"0"}</span>
                                        &nbsp;&nbsp;阅读
                                    </p> */}
                                </div>
                            )
                        })
                    }
                    {infoList.length !==0 &&
                        <div className="page">
                            <Pagination 
                                defaultCurrent={1} 
                                total={numFound} 
                                onChange= {this.pageChange}
                            />
                            {/* <div className="backTop" onClick={this.backTop}>
                                <img src="/static/images/feiji.png"/>
                            </div> */}
                        </div>
                    }
                </div>
                
            </div>
        )
    }
}
