import React from 'react';
import Loading from 'components/Loading';
import {AjaxByToken} from 'utils/ajax';
import moment from 'moment';
import {Link} from 'react-router';
import {Pagination,Modal } from 'antd';
import Share from 'components/share';
class PoliciesRegulations extends React.Component{
    constructor(){
        super();
        this.state={
            infoList:[],//政策法规信息列表
            numFound:"",//查询到的总数
            isLoading:false,
            isEmpty:false
        }
    }
    componentDidMount(){
        this.getzcfgInfoList('0') 
    }
    //获取政策法规信息列表
    getzcfgInfoList = (num) => {
        this.setState({
            isLoading: true
        })
        AjaxByToken('/mobile/api/ad_show/getWxInfo',{
            head: {
                transcode: 'F00011',
                type:"h"
            },
            data: {
                startIndex:num,
                pageNum:"10"
            }
        })
        .then(res=>{
            this.setState({
                infoList:res.data,
                numFound:res.numFound,
                isLoading: false,
                isEmpty:true
            })
        },err=>{
            Modal.error({
                title: `${err.data.returnMsg}`,
              });
        });
    }
    pageChange = (page, pageSize) => {
        this.getzcfgInfoList(`${((page-1)*10)}`);
        setTimeout(()=>{
            window.scroll(0,0)
        },100)
    };
    backTop = () => {
        window.scroll(0,0)
    }
    render(){
        const {infoList , numFound , isLoading , isEmpty} = this.state;
        return(
            <div className="contentSelect">
                <div className="content">
                    {
                        isLoading && <Loading/>
                    }
                    {
                        infoList.length ==0 && isEmpty && 
                        <div className="emptyIcon">
                            <img src="/static/images/emptyIcon.png"/>
                            <e className="emptyContent">暂时没有新的内容</e>
                        </div>
                    }
                    { infoList.length !==0 &&
                        infoList.map((item , index)=>{
                            return(
                                <div className="contentList">
                                    <Link 
                                        className="contentImg" 
                                        to={{
                                            pathname:"article",
                                            query:{
                                                id:`${item.infoid}`,
                                                title:`${item.groupname}`
                                            }
                                        }}
                                    >
                                        <img src={item.photourl?item.photourl:""} alt={item.title}/>
                                    </Link>
                                    <div className="contentDate">
                                        {item.pubdate?moment(item.pubdate).format("YYYY-MM-DD"):"暂无日期"}
                                    </div>
                                    <h2>
                                    <Link 
                                        to={{
                                            pathname:"article",
                                            query:{
                                                id:`${item.infoid}`,
                                                title:`${item.groupname}`
                                            }
                                        }}
                                    >
                                        {item.title?item.title:""}
                                    </Link>
                                    </h2>
                                    <p>
                                    <Link 
                                        to={{
                                            pathname:"article",
                                            query:{
                                                id:`${item.infoid}`,
                                                title:`${item.groupname}`
                                            }
                                        }}
                                    >
                                        {item.preview_text?`${item.preview_text.slice(0,45)}. . . . . .`:""}
                                    </Link></p>
                                    {/* <p className="readNumber"><span>{item.redsum?item.redsum:""}</span>&nbsp;&nbsp;阅读</p> */}
                                </div>
                            )
                        })
                    }
                    {infoList.length !==0 &&
                        <div className="page">
                            <Pagination 
                                defaultCurrent={1} 
                                total={numFound} 
                                onChange= {this.pageChange}
                            />
                            {/* <div className="backTop" onClick={this.backTop}>
                                <img src="/static/images/feiji.png"/>
                            </div> */}
                        </div>
                    }
                </div>
            </div>

        )
    }
}
export default PoliciesRegulations


